.btn-pink {
  background: linear-gradient(90.00deg, rgb(241, 56, 123) 30.208%,rgb(241, 56, 123) 30.218%,rgb(222, 69, 124) 100%);
  background-color: rgb(254, 95, 152);
  box-shadow: 0 0.9375rem 1.25rem 0 rgba(45, 61, 81, 0.2);
  border-radius: 1.25rem;
  span {
    font-family: Manrope;
    font-size: 1rem;
    line-height: 110%;
    font-weight: 700;
    text-transform: uppercase;
    color: rgb(253, 255, 252);
    padding: 1.875rem 0.5rem;
  }
  &:hover {
    background: rgb(254, 95, 152);
  }
  &:active {
    background: rgb(157, 164, 175);
  }
}
h2 {
  font-size: 3.625rem;
  line-height: 90%;
}
.form-group {
  border: none;
  padding: 0;
  margin: 0;
  input {
    width: 100%;
    height: 3.125rem;
    background: rgb(238, 238, 238);
    border: 0.0625rem solid rgb(157, 164, 175);
    border-radius: 0.9375rem;
    padding: 0 1.25rem;
    font-size: 1rem;
    line-height: 130%;
    font-weight: 500;
    color: rgb(65, 80, 103);
    &::placeholder {
      color: rgb(157, 164, 175) !important;
    }
  }
}
.agreement-check {
  input {
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  input:checked ~ label {
    &:before {
      opacity: 1;
    }
  }
  label {
    font-size: 0.875rem;
    line-height: 110%;
    font-weight: 300;
    color: rgb(65, 80, 103);
    padding-left: 1.875rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 0.875rem;
      height: 0.875rem;
      border: 0.0625rem solid #9DA4AF;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: -0.125rem;
      left: 0.125rem;
      width: 1.0625rem;
      height: 0.8125rem;
      background-image: url("../img/check3.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
      opacity: 0;
    }
  }
}
.button {
  display: flex;
  justify-content: center;
  align-items: center;
}
.left-anim {
  transform: translate(-60%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.left-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-anim {
  transform: translate(60%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.right-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.navigation {
  display: grid;
  grid-template-columns: 100%;
  gap: 1.5rem;
  padding: 3.125rem 0;
  a {
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 500;
    &:hover {
      color: #de457c;
    }
  }
}


@media screen and (max-width: 1399px) {
  h2 {
    font-size: 2.5rem;
  }
}

@media screen and (max-width: 1023px) {
  .btn-pink {
    span {
      font-size: 0.875rem;
      padding: 1.25rem 0.5rem;
    }
  }
  h2 {
    font-size: 2rem;
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 1.75rem;
  }
}