html {
  font-size: max(calc(16px * var(--coef_h)), 15px);
}
body {
  font-family: Manrope, sans-serif;
  background: #5884E7;
}
.fixed-body {
  overflow: hidden;
}
.wrapper {
  position: relative;
  overflow: hidden;
}
.container-fluid {
  max-width: 1700px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Tovari Sans;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  span {
    min-width: auto;
  }
}
img {
  object-fit: cover;
  display: block;
}