.footer {
  background: rgb(226, 243, 255);
  padding: 6.875rem 0 2.5rem 0;
  margin-top: -7.5rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 2.5rem;
  }
  &__left {
    display: flex;
    align-items: flex-end;
  }
  &__right {
    display: flex;
    flex-shrink: 0;
    gap: 2.5rem;
  }
  &__logo {
    width: 9.375rem;
    display: block;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__group {
    margin-left: 2.5rem;
  }
  &__text {
    p {
      font-size: 0.875rem;
      line-height: 160%;
      font-weight: 700;
      color: rgb(65, 80, 103);
      margin: 0;
    }
  }
  &__b2b {
    width: 11.875rem;
    margin-top: 0.75rem;
  }
  &__info {
    font-size: 0.875rem;
    line-height: 130%;
    font-weight: 700;
    color: rgb(65, 80, 103);
    margin-left: 2rem;
    a:hover {
      color: rgb(254, 95, 152);
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 1.25rem;
  }
  &__social {
    width: 2.25rem;
    height: 2.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(241, 56, 123);
    border-radius: 0.5rem;
    &:hover {
      background: rgb(254, 95, 152);
    }
  }
  &__wrap {
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
  }
  &__group2 {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__phone {
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 700;
    color: rgb(65, 80, 103);
    &:hover {
      color: rgb(254, 95, 152);
    }
  }
  &__btn {
    justify-content: flex-end;
    background: transparent;
    color: rgb(241, 56, 123);
    margin-top: 0.5rem;
    &:hover {
      color: rgb(254, 95, 152);
    }
    span {
      font-size: 0.875rem;
      line-height: 130%;
      font-weight: 700;
      text-decoration: underline;
      text-decoration-style: dashed;
      text-underline-offset: 0.1875rem;
      padding: 0;
    }
  }
  &__email-wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__email-title {
    font-size: 0.875rem;
    line-height: 130%;
    font-weight: 700;
    color: rgb(65, 80, 103);
  }
  &__email {
    font-size: 0.875rem;
    line-height: 130%;
    font-weight: 700;
    text-decoration: underline;
    text-decoration-style: dashed;
    text-underline-offset: 0.1875rem;
    color: rgb(241, 56, 123);
    &:hover {
      color: rgb(254, 95, 152);
    }
  }
  &__info-mob {
    display: none;
  }
}

@media screen and (max-width: 1399px) {
  .footer {
    &__inner {
      gap: 2rem;
    }
    &__group {
      margin-left: 2rem;
    }
    &__right {
      gap: 2rem;
    }
    &__socials {
      gap: 1rem;
    }
  }
}

@media screen and (max-width: 1300px) {
  .footer {
    &__right {
      flex-direction: column;
      gap: 1.25rem;
    }
    &__socials {
      order: 1;
      justify-content: flex-end;
    }
  }
}

@media screen and (max-width: 1180px) {
  .footer {
    &__phone {
      font-size: 1.25rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer {
    padding: 6.25rem 0 2rem 0;
    &__left {
      .footer__info {
        display: none;
      }
    }
    &__info-mob {
      display: block;
      margin-top: 2rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &__inner {
      align-items: start;
    }
    &__left {
      display: block;
    }
    &__group {
      margin: 2rem 0 0 0;
    }
  }
}

@media screen and (max-width: 499px) {
  .footer {
    &__inner {
      flex-direction: column;
    }
    &__group2 {
      align-items: start;
    }
    &__email-wrap {
      align-items: start;
    }
  }
}