#header {
  position: absolute;
  z-index: 10;
  top: 1.875rem;
  left: 50%;
  transform: translate(-50%, 0);
  width: calc(100% - 8.75rem);
  background: rgba(255, 253, 252, 1);
  border-radius: 2.1875rem;
  padding: 1.5rem 0 1rem;
  .header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 3.75rem;
  }
  .header__left {
    position: relative;
    width: 9.375rem;
  }
  .header__right {
    display: flex;
    align-items: center;
    gap: 3.75rem;
  }
  .header__logo {
    position: absolute;
    top: -3.125rem;
    left: 0;
    width: 100%;
    display: block;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 14rem;
      height: 12.5rem;
      background: rgb(255, 255, 255);
      box-shadow: 0.5rem 0.875rem 2.1875rem 0 rgba(45, 61, 81, 0.3);
      border-radius: 0 0 1.25rem 1.25rem;
    }
    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }
  }
  .header__socials {
    display: flex;
    align-items: center;
    gap: 1.25rem;
  }
  .header__social {
    width: 2.25rem;
    height: 2.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(241, 56, 123);
    border-radius: 0.5rem;
    &:hover {
      background: rgb(254, 95, 152);
    }
  }
  .header__email {
    display: flex;
    align-items: center;
    color: rgb(241, 56, 123);
    i {
      width: 2.25rem;
      height: 2.25rem;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgb(241, 56, 123);
      border-radius: 0.5rem;
      transition: all 0.3s;
      margin-right: 0.625rem;
    }
    span {
      font-size: 0.875rem;
      line-height: 130%;
      font-weight: 700;
      text-decoration: underline;
      text-decoration-style: dashed;
      text-underline-offset: 0.1875rem;
    }
    &:hover {
      color: rgb(254, 95, 152);
      i {
        background: rgb(254, 95, 152);
      }
    }
  }
  .header__group {
    display: flex;
    flex-direction: column;
  }
  .header__phone {
    font-size: 1.5rem;
    line-height: 135%;
    font-weight: 500;
    color: rgb(65, 80, 103);
    &:hover {
      color: rgb(254, 95, 152);
    }
  }
  .header__btn {
    justify-content: flex-end;
    background: transparent;
    color: rgb(241, 56, 123);
    &:hover {
      color: rgb(254, 95, 152);
    }
    span {
      font-size: 0.875rem;
      line-height: 135%;
      font-weight: 700;
      text-decoration: underline;
      text-decoration-style: dashed;
      text-underline-offset: 0.1875rem;
      padding: 0;
    }
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 2rem;
  height: 1.25rem;
  cursor: pointer;
  display: none;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 0.1875rem;
    background: rgb(241, 56, 123);
    border-radius: 0.1875rem;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 0.5rem;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 0.5rem;
      transform: rotate(45deg);
    }
  }
}


@media screen and (max-width: 1800px) {
  #header {
    width: calc(100% - 5rem);
  }
}

@media screen and (max-width: 1399px) {
  #header {
    width: calc(100% - 3.75rem);
    .header__left {
      width: 8.125rem;
    }
    .header__logo {
      top: 50%;
      transform: translate(0, -50%);
      &:after {
        width: 11.25rem;
        height: 11.25rem;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  #header {
    width: calc(100% - 3rem);
    .header__right {
      gap: 2.5rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  #header {
    .header__right {
      gap: 2rem;
    }
    .header__socials {
      gap: 0.875rem;
    }
    .header__phone {
      font-size: 1.25rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  #header {
    top: 1.25rem;
    width: calc(100% - 2rem);
    .header__inner {
      gap: 2.5rem;
    }
    .header__left {
      width: 6.25rem;
    }
    .header__logo {
      &:after {
        width: 9rem;
        height: 9rem;
      }
    }
    .header__email {
      span {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  #header {
    padding: 1.125rem 0;
    .header__left {
      width: 5rem;
    }
    .header__logo {
      &:after {
        width: 6.25rem;
        height: 6.25rem;
      }
    }
    .header__right {
      position: fixed;
      z-index: 10;
      top: -1.25rem;
      left: -1rem;
      width: calc(100% + 2rem);
      height: calc(100vh + 1.25rem);
      flex-direction: column;
      background: #ffffff;
      padding: 7.5rem 1rem 3.125rem;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
    .header__email {
      span {
        display: block;
      }
    }
    .header__btn {
      justify-content: center;
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 359px) {
  #header {
    .container-fluid {
      padding: 0 1.5rem;
    }
  }
}