      margin: 0 auto;
    }
    &__group2 {
      margin-top: 1.5rem;
    }
    &__text {
      min-height: 9rem;
    }
    &__img {
      display: none;
    }
  }
  .services {
    &__img2 {
      top: -2rem;
    }
  }
  .service-card {
    &:after {
      width: calc(100% - 0.625rem);
      height: calc(100% - 0.625rem);
    }
    &__desc {
      padding: 0 1rem 1rem;
      margin-top: 1rem;
    }
    ul {
      li {
        font-size: 0.75rem;
        i {
          width: 1rem;
          margin-right: 0.625rem;
        }
      }
    }
  }
  .services2 {
    padding-bottom: 4rem;
    &__img {
      bottom: 6.25rem;
      max-width: 30rem;
    }
  }
  .discount {
    &__desc {
      width: 60%;
      padding: 1.5rem 0 1.5rem 1.5rem;
    }
    &__img {
      width: 40%;
    }
  }
  .feedbacks {
    padding: 6rem 0 10rem;
    &__slider {
      max-width: 18rem;
    }
  }
}

@media screen and (max-width: 399px) {
  .main-section {
    &__items {
      grid-template-columns: 100%;
    }
  }
  .info-slide {
    &__group2 {
      max-width: 20rem;
    }
  }
  .services {
    p {
      br {
        display: none;
      }
    }
    &__img3 {
      bottom: calc(100% - 9.25rem);
    }
    &__img4 {
      bottom: calc(100% - 12rem);
    }
  }
  .service-card {
    &__title {
      font-size: 1.125rem;
    }
    &__text {
      font-size: 0.8125rem;
    }
  }
  .services2 {
    &__cards {
      max-width: 15rem;
      grid-template-columns: 100%;
    }
  }
  .discount {
    &__desc {
      width: 100%;
    }
    &__img {
      display: none;
    }
  }
  .info3 {
    padding-top: 5rem;
    &__cards {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 1rem / 2));
    }
  }
  .info3-card {
    flex-grow: unset;
    flex-basis: unset;
    &__title {
      font-size: 1.125rem;
    }
    &__text {
      p {
        font-size: 0.75rem;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .info-slide {
    &__group2 {
      max-width: 17rem;
    }
  }
}